import { Col, Row } from 'antd';
import { Layout } from 'antd';
import * as React from 'react';
import { } from 'react-router';
import pic1 from '../../images/welpen1k.jpg'
import pic2 from '../../images/Welpen2k.jpg'

import './Junghunde.scss';
// const { Header, Footer, Sider, Content } = Layout;

interface IProps {
  name: string;
  age?: number;
}

class Junghunde extends React.Component<IProps, object> {
  public render() {
    // let test = { titel: 'hallo' };
    // const test3 = test.titel;
    // test = test;
    // const ageX10 = this.props.age ? this.props.age * 10 : 0;

    return (
      <Layout className='container'>
        <Row type={'flex'} justify="center">
          <Col className='center' span={24}>
            <h1 style={{ color: '#D7A200' }}>Grundausbildung</h1>

          </Col>
        </Row>
        <br/>
        <Row>

          <Col xl={{ span: 10 }} xxl={{ span: 15 }}>
            <Row type='flex' >
            <div className='citeBox2'> Junghunde</div>
              <div className='citeBox2'>               
                <div>
                Wenn der Welpe ins „Flegelalter“ kommt, braucht es viel Geduld und Beständigkeit, ihm seinen Rang im Rudel klarzumachen. In dieser Entwicklungsphase will er täglich aufs Neue seine Grenzen ausloten. Durch unmissverständliche Kommandos und deutliche Körpersprache kann dem Hund geholfen werden, zu lernen, was erwünscht und was unerwünscht ist. Lernziele sind darüber hinaus lockeres An-der-Leine-Gehen und freies Bei-mir-Gehen – auch unter Ablenkung durch Artgenossen, andere Menschen etc. – sowie sicherer Rückruf und Freiablage.
Wir trainieren maximal sechs Hunde pro Gruppe/Stunde, um auf jedes Mensch-Hund-Team individuell eingehen zu können.
              </div>
              </div>
            </Row>
          </Col>




          <Col>
            <div className='colRight'>
              <div>
                <img className='image20' src={pic1} alt='bild' />
              </div>
              </div>
              <div className='colRight'>
              <div>
                <img className='image20' src={pic2} alt='bild' />
              </div>
              </div>
          </Col>

        </Row>

      </Layout>
    );
  }
}

export default Junghunde;



